import React from 'react';
import './style.css'
import imagenAbout from '../assets/a8ccs-gcnmr.svg';

const AboutMe =()=> {

    return (
        <section className='section'>
            <div className='section__about'>
                <div className='section-card'>
                    <h2>Sobre Mi</h2>
                    <div className='section-text'>
                        <p>El amor al maquillaje empez como una va de escape de ciertas situaciones que pase en 2016, despus del primer curso de maquillaje al que asist descubr que me gustaba y que era buena haciendo maquillajes. Continue practicando y cursando mas cursos de maquillaje profesional, preparndome, despus empec a atender a clientas y una de ellas me insisti en que le dictara un curso de automaquillaje y fue as como empec a impartir mis conocimientos sobre el maquillaje, tiempo despus trabaje en una academia de maquillaje dictando cursos tantos bsicos como profesionales.
                        Estoy convencida en que cada uno tiene un don en particular y el mo es impartir mis conocimientos de maquillaje tanto para personas que quieran aprender tcnicas bsicas para un maquillaje diario, personas que quieren formarse profesionalmente en este hermoso mundo del maquillaje, as como tambin personas que quieren que les realice un maquillaje para cualquier ocasin.
                        Confa en el don que tienes y consigue las destrezas necesarias para convertirte en quien deseas ser.
                        Atrvete con Mai_visage
                        </p>
                    </div>
                    <div className='section-card-img'>
                        <img src={imagenAbout} className='card-img' alt='sobreMi'/>
                    </div>
                    
                </div>
            </div>        
        </section>

    )
}

export default AboutMe;